import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatTableDataSource } from '@angular/material/table';
import { FormListComponent } from '@fragments';
import { ApiService, SweetAlertService } from '@services';
import { ENDPOINTS } from '@shared';
import { NumberOfMultipleApplicationAllowed } from 'app/core/models/numberOfMultipleApplicationAllowed.model';
import { ValidateFieldService } from 'app/core/services/validate-field.service';

@Component({
  selector: 'app-number-of-multiple-application-allowed',
  templateUrl: './number-of-multiple-application-allowed.component.html',
  styleUrls: ['./number-of-multiple-application-allowed.component.scss']
})
export class NumberOfMultipleApplicationAllowedComponent implements OnInit {
  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  dataSource: MatTableDataSource<NumberOfMultipleApplicationAllowed>;
  displayedColumns: string[] = ['minimum', 'maximum'];

  numberOfMultipleApplicationAllowed = new NumberOfMultipleApplicationAllowed();
  numberOfMultipleApplicationAllowedList = [];
  numberOfMultipleApplicationAllowedForm: FormGroup;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    public validateFieldService: ValidateFieldService,
  ) { }

  ngOnInit(): void {
    this.getList();
    this.setFormValidator();
  }

  getList(): void {
    this.apiService.findAll(ENDPOINTS.noOfMultipleApplicationAllowed).subscribe((res: any) => {
      this.numberOfMultipleApplicationAllowed = res.responseData.data;
      this.dataSource = new MatTableDataSource<NumberOfMultipleApplicationAllowed>(res.responseData.data);
      this.dataSource.paginator = this.formListComponent.paginator;
      this.dataSource.sort = this.formListComponent.sort;
    });
  }

  save():void {
    this.apiService.save(ENDPOINTS.noOfMultipleApplicationAllowed, this.numberOfMultipleApplicationAllowed).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        err.error.message = 'Please enter numeric values only';
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.resetFormValidator();
        this.getList();
      });
  }

  update():void {
    this.apiService.update(ENDPOINTS.noOfMultipleApplicationAllowed, this.numberOfMultipleApplicationAllowed).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.success(res);
        }
      }, (err: any) => {
        this.sweetAlertService.error(err);
      }, () => {
        // actions after subscription
        this.resetFormValidator();
        this.getList();
      });
  }

  getNumberOfMultipleApplicationAllowed(){
    this.apiService.findById(ENDPOINTS.noOfMultipleApplicationAllowed, this.formListComponent.idForUpdate).subscribe((res: any) => {
      if (res) {
        this.numberOfMultipleApplicationAllowed = res.responseData.data;
      }
    });
  }


  setFormValidator(): void {
    this.numberOfMultipleApplicationAllowedForm = this.formBuilder.group({
      minimum: ['', Validators.required],
      maximum: ['', Validators.required],
    });
  }

  resetFormValidator(): void {
    this.numberOfMultipleApplicationAllowed = new NumberOfMultipleApplicationAllowed();
    this.numberOfMultipleApplicationAllowedForm.markAsUntouched();
    this.numberOfMultipleApplicationAllowedForm.markAsPristine();
  }

}
